  @include mandatory-label;
}

.form-input-container {
  &.edit-mode-off input {
    -moz-appearance: textfield !important;
  }

  .form-input {
    background-color: transparent !important;
  }

  .ant-input-group-wrapper {
    padding: 0px !important;
    input,
    .ant-input-group-addon {
      border: none !important;
      background-color: transparent !important;
    }
    .ant-input-group-addon {
      padding: 0px 10px 0px 0px !important;
    }
  }

  &:not(.single-line-mode) .ant-form-item-row {
    display: block !important;
  }
  &.single-line-mode .ant-form-item-row {
    display: flex !important;
    flex-direction: row !important;
    align-items: center !important;
    & .ant-form-item-label {
      label {
        margin: 2px 0px 0px 0px !important;
      }
    }
  }

  &.invert-mode {
    &:not(.edit-mode-off) .form-input {
      border: 1px solid $white !important;
    }
    .ant-form-item-label label {
      line-height: 12px;
    }
    .ant-form-item-label label,
    input {
      color: $white !important;
      &::placeholder {
        color: $white;
        opacity: 0.8;
        font-weight: 600;
      }
    }
  }

  .ant-form-item-label label::after {
    margin: 0px 3px !important;
    line-height: 12px;
    // display: none;
  }

  .ant-form-item-label label::before {
    // margin: 0px 3px !important;
    display: none !important;
  }
  align-items: center;
  &:not(.edit-mode-off) .form-input:focus {
    outline: 0 !important;
    border: 1px solid $info !important;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25) !important;
  }
  &.edit-mode-off .form-input:focus {
    outline: 0 !important;
    box-shadow: none !important;
  }
  &:not(.edit-mode-off) .form-input {
    &:not(
        &.ant-picker-status-error,
        &.ant-input-status-error,
        &.ant-input-status-error
      ) {
      &:not(:focus) {
        border: 1px solid $muted !important;
      }
    }
    &.ant-picker-status-error,
    &.ant-input-status-error {
      border: 1px solid $danger !important;
    }
    border-radius: 4px !important;
    // background-color: transparent !important;

    &::placeholder {
      font-style: normal;
      font-weight: 600;
      font-size: calculateFontSize(14px);
      line-height: 14px;
      color: $dark;
      opacity: 0.25 !important;
    }
  }

  & .ant-form-item-label {
    height: auto !important;
    & label:not(& label.ant-form-item-required) {
      font-size: calculateFontSize(12px);
      line-height: 18px;
      height: 1.3rem !important;
      color: $label;
      display: flex;
      // flex-direction: row-reverse;
      align-items: center;
      margin-bottom: 2px;
      .required {
        @include mandatory-field;
      }
    }
    // label:has(.required),
    label.ant-form-item-required {
      @include mandatory-label;
      .required {
        @include mandatory-field;
      }
    }
  }

  .form-input:not(.ant-input-password, .ant-input-affix-wrapper),
  .label-mode {
    display: block;
    width: 100%;
    font-style: normal;
    font-weight: 600;
    font-size: calculateFontSize(14px);
    color: $dark;
    &.form-input {
      padding: 4px 5px;
    }
  }

  input.ant-input-disabled {
    background-color: $muted-rgb-6 !important;
  }

  &.edit-mode-off {
    .ant-form-item-control-input {
      min-height: auto !important;
      height: 12px;
      // pointer-events: none !important;
    }
    & {
      flex-wrap: nowrap !important;
    }
    .form-input {
      border: 0px solid $white !important;
    }
    input {
      padding: 0px !important;
    }
  }
}

.form-textarea-container {
  height: auto !important;
  textarea {
    overflow: hidden;
    min-height: 28px !important;
  }
  &.edit-mode-off {
    .ant-form-item-control {
      textarea {
        padding: 0px !important;
