    border: 1px solid $white !important;
    & span {
      color: $white !important;
    }
  }
  & span {
    font-style: normal;
    font-weight: 400;
    font-size: calculateFontSize(18px);
    color: $white;
    margin-bottom: 3px;
  }
  &:not(:disabled) {
    &.variant-primary {
      border: 1px solid $header;
      &.ant-btn-primary {
        background-color: $header !important;
      }
      &.ant-btn-default {
        background-color: transparent !important;
        & span {
          color: $header;
        }
      }
    }
    &.variant-danger {
      border: 1px solid $danger-1;
      &.light-mode {
        border: 1px solid $danger-2 !important;
        & span {
          color: $danger-2 !important;
        }
      }

      &.ant-btn-primary {
        background-color: $danger-1 !important;
      }
      &.ant-btn-default {
        background-color: transparent !important;
        & span {
          color: $danger-1;
        }
      }
    }
    &.variant-success {
      border: 1px solid $success;
      &.light-mode {
        border: 1px solid $success-1 !important;
        & span {
          color: $success-1 !important;
        }
      }
      &.ant-btn-primary {
        background-color: $success !important;
      }
      &.ant-btn-default {
        background-color: transparent !important;
        & span {
          color: $success;
        }
      }
    }
  }
  &:disabled {
    border: 1px solid $disabled !important;
    & span {
      color: $muted !important;
    }
    background-color: $disabled !important;
  }
}

.shipment-timeline-container {
