.menu-header {
  background-color: $dark-1 !important;
  // filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.primary-menu-container {
  background-color: $header !important;
  padding: 10px 30px;
  // height: 60px;

  .right-contents-container > div {
    line-height: 0px;
  }
  & ul {
    background-color: transparent !important;
    border: none !important;
  }

  .profile-avatar img {
    object-fit: center;
  }

  .profile-drop-down-container {
    cursor: pointer;

    .profile-name {
      font-style: normal;
      font-weight: 600;
      font-size: calculateFontSize(12px);
      color: $topMenuText;
    }

    .text-container {
      @media screen and (min-width: 1000px) {
        display: block;
      }

      @media screen and (max-width: 999px) {
        display: none;
      }
    }
  }
  .website-container {
    display: flex;
    align-items: center;
    .website-name {
      color: $white;
      font-size: calculateFontSize(20px);
      font-weight: 600;
    }
  }

  .top-menu-button-container svg {
    path {
      fill: $topMenuText !important;
    }
    rect {
      stroke: $topMenuText !important;
    }
  }
}
.profile-drop-down {
  background-color: #fff;
}
.secondary-menu-container {
  padding: 9px 30px;
  .secondary-menu {
    display: flex;
    align-items: center;
    height: 100%;
    background-color: transparent !important;
    border: none;

    & li {
      margin: 0px !important;
      padding: 0px !important;
    }
    & li::after {
      display: none;
    }
    & li:not(:first-child) a {
      margin-left: 16px;
    }
    & li {
      a {
        font-style: normal;
        font-weight: 700;
        font-size: calculateFontSize(16px);
        line-height: 24px;
        color: $bottomMenuText;
      }
      a.active,
      a:hover {
        color: $muted;
      }
    }
    .ant-menu-submenu-title {
      margin-left: 16px;
      .overflow-indicater {
        color: $white;
      }
    }
  }

  .ant-menu-submenu-title {
    margin: 0px !important;
  }
}

.ant-menu-submenu.secondary-menu-sub {
  border-radius: 5px;
  background-color: $dark-1;
  ul {
    background-color: transparent !important;
    min-width: 185px !important;
    & li {
      margin: 3px 0px !important;
      padding: 0px !important;
      height: auto !important;
      line-height: inherit !important;
      &,
      &:active {
        background-color: transparent !important;
      }
    }
    & li::after {
      display: none;
    }
    & li a {
      margin-left: 16px;
      margin-right: 16px;
    }
    & li {
      a {
        font-style: normal;
        font-weight: 700;
        font-size: calculateFontSize(16px);
        line-height: 24px;
        color: $white !important;
      }
      a.active,
      a:hover {
        color: $muted !important;
      }
    }
  }
}

.secondary-menu-overflow-popu {
  ul {
    background-color: $dark !important;
  }
  & li {
    background-color: transparent !important;
    margin: 0px !important;
    padding: 0px !important;

    &.ant-menu-item-selected {
      a {
        color: $muted !important;
      }
    }
  }
  & li::after {
    display: none;
  }
  & li {
    a {
      font-style: normal;
      font-weight: 700;
      font-size: calculateFontSize(14px);
      line-height: 19px;
      color: $white;
      margin-left: 16px;
    }
    a.active,
    a:hover {
      color: $muted;
    }
  }
}

.header-search-box-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header-search-box {
  height: 100%;
  background-color: $grey-2;
  border: none !important;
  height: 25px;
  border-radius: 14px;
  line-height: 0px;
  padding: 5px 15px;
  box-shadow: none !important;

  &,
  &::placeholder {
    font-style: normal;
    font-weight: 600;
    font-size: calculateFontSize(14px);
    color: $white;
    line-height: 21px;
  }
}

.menu-client-select {
  @media screen and (min-width: 768px) {
    display: block;
  }

  @media screen and (max-width: 767px) {
    display: none;
  }
  .client-selector .ant-select-selector {
    width: 350px;
    height: 100% !important;
    border-radius: 5px;
  }
  .ant-select-selection-placeholder {
    font-style: normal;
    font-weight: 600;
    font-size: calculateFontSize(12px);
    line-height: 20px;
    color: $header;
  }
  .ant-select-selection-item,
  .company-select-option {
    line-height: 0px !important;
  }
  input {
    height: 100% !important;
  }
  .ant-select-selection-item {
    display: contents !important;
  }
}

.dynamic-font-size-container {
  .text-container {
    button {
      color: $muted;
      &:hover,
      &:active,
      &:focus,
      &.active {
        color: $topMenuText;
      }
      font-weight: 600;
    }
    div:nth-child(1) button {
      font-size: 13px !important;
    }
    div:nth-child(2) button {
      font-size: 16px !important;
    }
    div:nth-child(3) button {
      font-size: 19px !important;
    }
  }
  .line-container {
    margin-top: 3px;
    height: 1px;
    background-color: $muted;
    display: flex;
    justify-content: space-between;
    span {
      display: inline-block;
      height: 5px;
      width: 5px;
      background-color: $muted;
      border-radius: 5px;
      position: relative;
      &:nth-child(2) {
        margin-left: -3px;
      }
      top: -2px;
      &.active {
        background-color: $topMenuText !important;
      }
    }
  }
}

.profile-overlay-container {
  .ant-dropdown-arrow::before {
    background-color: $topMenuText !important;
  }
}

.ant-modal-content {
  padding: 0px !important;
}
