@import "./root.scss";
// colors
$muted: var(--muted);
$dark: var(--dark);
$dark-1: var(--dark-1);
$danger: var(--danger);
$danger-1: var(--danger-1);
$primary: var(--primary);
$info: var(--info);
$warning: var(--warning);
$white: var(--white);
$light: var(--light);
$success: var(--success);
$grey: var(--grey);
$grey-1: var(--grey-1);
$grey-2: var(--grey-2);
$grey-3: var(--grey-3);
$header: var(--header);
$black: var(--black);
$highlight: var(--highlight);
$label: var(--label);
$muted-rgb-6: var(--muted-rgb-6);
$danger-2: var(--danger-2);
$danger-3: var(--danger-3);
$success-1: var(--success-1);
$border: var(--border);
$disabled: var(--disabled);
$disabled-text: var(--disabled-text);
$grey-4: var(--grey-4);
$selected: var(--selected);
$fontSizePercent: var(--fontSizePercent);
$dark-footer-2: var(--dark-footer-2);
$tab-border: var(--tab-border);
$distanceColor: var(--distanceColor);
$statusColor: var(--header);
$iconDanger: var(--iconDanger);
$iconSuccess: var(--iconSuccess);

// fonts
$font-family-inter: "Inter", Helvetica;
$font-family: var(--font-family);
$inputsHeight: var(--inputsHeight);
$invalidInput: var(--invalidInput);
$topMenuText: var(--topMenuText);
$bottomMenuText: var(--bottomMenuText);

$colors: (
  "muted": $muted,
  "dark": $dark,
  "dark-1": $dark-1,
  "danger": $danger,
  "danger-1": $danger-1,
  "danger-2": $danger-2,
  "danger-3": $danger-3,
  "primary": $primary,
  "info": $info,
  "warning": $warning,
  "white": $white,
  "light": $light,
  "success": $success,
  "success-1": $success-1,
  "grey": $grey,
  "grey-1": $grey-1,
  "grey-2": $grey-2,
  "header": $header,
  "highlight": $highlight,
  "label": $label,
  "border": $border,
  "grey-4": $grey-4,
  "black": $black,
  "disabled": $disabled,
  "disabled-text": $disabled-text,
  "selected": $selected,
  "dark-footer-2": $dark-footer-2,
  "tab-border": $tab-border,
  "invalidInput": $invalidInput,
  "grey-3": $grey-3,
  "topMenuText": $topMenuText,
  "bottomMenuText": $bottomMenuText,
  "distanceColor": $distanceColor,
  "statusColor": $statusColor,
  "iconDanger": $iconDanger,
  "iconSuccess": $iconSuccess,
);

@function calculateFontSize($size) {
  @return calc(#{$size} + #{$fontSizePercent});
}

@each $prefix, $value in $colors {
  .background-#{$prefix} {
    background-color: #{$value} !important;
  }
  .text-#{$prefix} {
    color: #{$value} !important;
  }
  .border-#{$prefix} {
    border: 1px solid #{$value} !important;
  }

  .border-bottom-#{$prefix} {
    border-bottom: 1px solid #{$value} !important;
  }

  .border-top-#{$prefix} {
    border-top: 1px solid #{$value} !important;
  }

  .border-right-#{$prefix} {
    border-right: 1px solid #{$value} !important;
  }

  .border-left-#{$prefix} {
    border-left: 1px solid #{$value} !important;
  }
}

@function calculateHeight($size) {
  @return calc(#{$size} + #{$fontSizePercent});
}
